import React, { useState, useEffect } from "react";
import { axiosInstance1 } from "../axiosComponents/axiosAPIContUs";

export const ProjectsComponent = ({ match }) => {
  const [projectData, setProjectData] = useState('');
    useEffect (() => {
        document.title = "TLSE: Project";
        //document.getElementById('current-project-page').style = 'background: #eee;';
        try { const response = axiosInstance1.get('/currentprojects/'+ match.params.projectID)  
        .then((response) => {
          setProjectData(response.data);
          console.log(projectData);
        }) 
        return projectData;
    } catch (error) {
        throw error
     }
     }, [])
    return (
      <div className="current-project-page" id="current-project-page">
        <div className='project-container container'>
          <div className='project-data-holder col-12'>
            <h1>{projectData.title}</h1>
            <p>{projectData.content}</p>
          </div>
          <hr/>
          <div className='project-imgs-holder col-12'>
            <div className='project-img-holder'>
            <img src='../logos/ccfvrb.png' />
            </div>
            <div className='project-img-holder'>
            <img src='../logos/ccfvrb-examplelargevideos.png' />
            </div>
            <div className='project-img-holder'>
            <img src='../logos/ccfvrb-examplesmallvideos.png' />
            </div>
            <div className='project-img-holder'>
            <img src='../logos/ccfvrb-examplefullpageopensections.png' />
            </div>
          </div>
          
        </div>
      </div>
    )
}