import React, { Component } from 'react'

class MovingScreen extends Component {
    constructor(props) {
        super(props);

        const images = [
            "<img id='tlse-logo-home' src='/logos/logotlse.png'>",
            "<div class='row carousel-text2'><h3>Built using</h3></div><div class='row'><div class='container logo-dev'><div class='col-4'><img src='/logos/react-logo.png'></div><div class='col-4'><img style='width: 100%' src='/logos/django-logo.png'></div><div class='col-4'><img style='width: 100%' src='/logos/drf.png'></div><div class='col-4'><img src='/logos/postgres-logo.png'></div></div></div>",
            "<div class='container description-con'><div class='description-slide'>More coming soon...</div></div>"
        ];

        this.state = {
            images,
            currentImg: 0
        }
    }
    componentDidMount() {
        this.interval = setInterval(() => this.changeBackgroundImage(), 5000);
    }

    componentWillUnmount() {
        if (this.interval) {
            clearInterval(this.interval);
        }
    }

    changeBackgroundImage() {
        let newCurrentImg = 0;
        const {images, currentImg} = this.state;
        const noOfImages = images.length;

        if (currentImg !== noOfImages - 1) {
            newCurrentImg = currentImg + 1;
        }

        this.setState({currentImg: newCurrentImg});
    }
    render() {
        const {images, currentImg} = this.state;
        let urlString = images[currentImg];
        

        return (
        <div className='dynamicTitle'>
            <div className="text-holder" dangerouslySetInnerHTML={{__html: urlString}}></div>
            </div>
        );
}
}
export default MovingScreen
