import React from 'react';


function getCookie(name) {
    let cookieValue = null;
    if (document.cookie && document.cookie !== '') {
        const cookies = document.cookie.split(';');
        for (let i = 0; i < cookies.length; i++) {
            const cookie = cookies[i].trim();
                    // Does this cookie string begin with the name we want?
                    if (cookie.substring(0, name.length + 1) === (name + '=')) {
                        cookieValue = decodeURIComponent(cookie.substring(name.length + 1));
                        break;
                    }
                }
            }
            return cookieValue;
        }

//function lookup(method, endpoint, callback, data)
function lookup(method, endpoint, callback, data) {
    let jsonData;
    if (data) {
        jsonData = JSON.stringify(data)
        return jsonData
            }
    const url = `http://localhost:8000/api${endpoint}`
    const csrftoken = getCookie('csrftoken')
    fetch(url, {
        method: `${method}`,
        headers: { "X-CSRFToken": csrftoken,
                    'Content-Type': 'application/json',
                    'Authorization': localStorage.getItem('access_token') ? "JWT " + localStorage.getItem('access_token') : null,
                    'Accept': 'application/json' },})
        .then((response) => {
            if (response.ok) {
                return response.json();
            } else {
                return Promise.reject('something went wrong!')
        }
      })
        .then((data) => {;
            console.log(data);
            callback(data);
        })
        .catch(error => console.log('error is', error));
}

export function createBlogPost(newBlogPost) {
    lookup("POST", "/blogposts/create/", {content: newBlogPost.content, user: newBlogPost.user})
}
export function loadBlogPosts(callback) {
        lookup("GET", "/blogposts/", callback)
}

export function loadBlogPostsProfile(callback) {
    lookup("GET", "/blogposts/", callback)
}

export function loadCurrentProjects(callback) {
    lookup("GET", "/currentprojects/", callback)
}
