import React from 'react';
import {CurrentProjectsList} from './CurrentProject';


export function CurrentProjects() {
    return (
        <div id="currentprojects">
            <div className="container projects-container">
            <div className="col-2 title-container">
            <h1>Current Projects</h1>
            </div>
            <div className="col-10 portfolio-container">
                <div className="tlse-in-space">
                    <div className="wowoops"><img className="wow" alt="wow" src="./logos/wow.png"/>
                    <img src="./logos/tlseinspace.png" alt="tlse as an astronaut"/>
                    <img className="oops" src="./logos/oops.png" alt="oops"/></div>
                    
                </div>  
            <CurrentProjectsList/>
            </div>
            </div>
            </div>
    )
    } 