import React, { useState } from "react";
import { axiosInstance } from '../axiosComponents/axiosApi';

export const Signup = (props) => {
    const [username, setUsername] = useState("");
    const [password, setPassword] = useState("");
    const [email, setEmail] = useState("");
    const [errors, setErrors] = useState("");

    const handleChange = (e) => {
      if (e.target.name === "username") {
        setUsername(e.target.value);
      } else if (e.target.name === "password") {
        setPassword(e.target.value);
      } else if (e.target.name === "email") {
        setEmail(e.target.value);
      }
    }

    const handleSubmit = (e) => {
        e.preventDefault();
        try {
          const response = axiosInstance.post('/user/create/', {
              username: username,
              email: email,
              password: password
          });
          return response;
      } catch (error) {
           //console.log(error.stack);
           setErrors(error.response.data);
           console.log(errors);
      }
      return errors;
    }

        return (
          <div class="form-container sign-up-container">
		        <form onSubmit={handleSubmit}>
			        <h1>Create Account</h1>
			        <div class="social-container">
                        <a href="#" class="social"><i class="fab fa-facebook-f"></i></a>
                        <a href="#" class="social"><i class="fab fa-google-plus-g"></i></a>
                        <a href="#" class="social"><i class="fab fa-linkedin-in"></i></a>
			        </div>
                    <span>or use your email for registration</span>
                    <input name="username" type="text" autoComplete="signup username"value={username} onChange={handleChange}/>
                    <input name="email" type="email" autoComplete="signup email" value={email} onChange={handleChange}/>
                    <input name="password" type="password" autoComplete="signup password" value={password} onChange={handleChange}/>
                    <button><input type="submit" value="Submit"/>Sign Up</button>
                </form>
	        </div>
        )
    }