import React, { useEffect } from 'react';
 
export const About = () => {
   useEffect (() => {
      document.title ="TLSE: About Me";
   }, [])
    return (
       
       <div className="about-us-body ">
          <div className="d-flex">
             <div className="col-7 about-me-col">
                <h2>Hello, I am Tom Lewis</h2>
                <h5>I'm a Software Developer</h5>
                <p>I have been working for 3 years as a Software Developer within the education sector. Specialising in building Web Applications using React, Django, PHP, Python, Moodle and more.</p>
             </div>
             <div className="col-5">
             <img className="me-logo" src="./logos/me.png" alt="Picture of Tom Lewis"/>
             </div>
          </div>
          <hr className="mt-0 mb-6"/>
          <div className="photo-row container">
                   <div className="small-photo sp-1">
                      <img src="./logos/mebeckycanadasnow.jpg"/>
                   </div>
                   <div className="small-photo sp-2">
                      <img src="./logos/meandbaby.jpg"/>
                   </div>
                   <div className="small-photo sp-3">
                      <img src="./logos/snowday.jpg"/>
                   </div>
                   <div className="small-photo sp-4">
                      <img src="./logos/meniagara.jpg"/>
                   </div>
             </div>
             </div>
    );
}