import React, {useEffect, useState} from 'react'
import { axiosInstanceBlog } from '../axiosComponents/axiosApiBlog';
import { axiosInstance } from '../axiosComponents/axiosApi'
import { axiosInstanceComment } from '../axiosComponents/axiosAPIComment';
import { Collapse, Button } from 'reactstrap';

import {loadBlogPosts} from '../lookup'
import {Crown, Poop} from './extracomponents/svgs'


function createBlogPost(form_data_insert) {
    try {
        const response = axiosInstanceBlog.post('/blogposts/create/', form_data_insert)
    .then((response) => {
        if (response.status === 201) {
            //window.location.href = '/sent';
            document.getElementById("blog-post-section").style.display = "none";
            document.getElementById("post-added").style.display = "block";
            setTimeout(function(){
                window.location.href = '/blog';
             }, 3000);
        }
    })
    return response.data; } catch (error) {
        throw error;
    }
  }


export function BlogsComponent(props) {
    useEffect (() => {
        document.title ="TLSE: The Techy Blog";
     }, [])
    const textAreaRef = React.createRef()
    const [newBlogPosts, setNewBlogPosts] = useState([])
    const [userId, setUserId] = useState ('');
    useEffect(() => {
        const accessToken = localStorage.getItem('access_token');
        try {
          const response = axiosInstance.get('/currentuser/')
        .then((response) => {
            axiosInstance.defaults.headers['Authorization'] = "JWT " + accessToken;
            setUserId(response.data.id)
            //console.log(userId);
        });
        return userId 
        } catch (error) {
        throw error;
        }
      }, [userId]);

    const handleSubmit = (event) => {
        event.preventDefault()
        const newVal = textAreaRef.current.value
        let tempNewBlogPosts = [...newBlogPosts]
        let form_data = new FormData();
        form_data.append('user', userId);
        form_data.append('content', newVal);
        createBlogPost(form_data);
        setNewBlogPosts(tempNewBlogPosts)
        textAreaRef.current.value = ''
    }
    return (
    <div className="blog-page container" id="blog-posts">
              <h1> My Developer Blog</h1>
              <div id="post-added">
                <h1>Message Sent</h1>
              </div>
              <div id="blog-post-section"></div>
                <div className={props.className}>
                            <div className='col=12 mb-3'>
                                <form onSubmit={handleSubmit}>
                                    <textarea ref={textAreaRef} required={true} className='form-control' name='tweet'>

                                    </textarea>
                                    <button type='submit' className='btn btn-primary my-3'>Post</button>
                                </form>
                            </div>
                    <BlogPostsList newBlogPosts={newBlogPosts}/>
                        </div>
                        </div>
    )
}

export function BlogPostsList(props) {
    const [blogPostsInit, setBlogPostsInit] = useState([])
    const [blogPosts, setBlogPosts] = useState([])
    const [blogPostsDidSet, setBlogPostsDidSet] = useState(false)
    //setTweetsInit([...props.newTweets.concat(tweetsInit)])
    useEffect(() => {
        const final = [...props.newBlogPosts].concat(blogPostsInit)
        //console.log(final)
        if (final.length !== blogPosts.length) {
            setBlogPosts(final)
        }
    }, [props.newBlogPosts, blogPosts, blogPostsInit])
  useEffect(() => {
      const dataUpdate = (datatest) => {
        setBlogPostsInit(datatest);
        setBlogPostsDidSet(true)
      }
      loadBlogPosts(dataUpdate);
    }     
  , [setBlogPostsInit, blogPostsDidSet, setBlogPostsDidSet, loadBlogPosts])
    return blogPosts.map((item, index)=>{
          return <BlogPost blogPost={item} className={'blog-post-card'} key={`${index-item.id}`}/>
            })
}

export function ActionBtn(props) {
    const {blogPost, action} = props
    const [likes, setLikes] = useState(blogPost.likes ? blogPost.likes : 0)
    const [userLike, setUserLike] = useState(blogPost.userLike === true  ? true : false)
    const className = props.className ? props.className : 'btn btn-primary btn-sm';
    const actionDisplay = action.display ? action.display : 'Action'

    const handleClick = (event) => {
        event.preventDefault()
        if (action.type === 'like') {
            if (userLike === true) {
                setLikes(likes-1)
                setUserLike(false)
            } else {
                setLikes(likes+1)
                setUserLike(true)
            }

        }
    }
    const displaytype = action.type === 'like' ? `${likes} ${actionDisplay}` : actionDisplay;
    if (action.type === 'like'){
        return (<button className={className} onClick={handleClick}><Crown/>{displaytype}</button>)
    } else {
        return (<button className={className} onClick={handleClick}><Poop/>{displaytype}</button>)
    }
}

export function Comment(props) {
    const comments = props.comment
    return (
        comments.length
            ? comments.map(comments=> (
                <div className="card" key={comments.id}>
                    <div className="comments-row">
                        <div className="col-3 p-3">
                            <div className="user-image"><img src='./logos/logotlse.png'/></div>
                            <div className="comment-username">{comments.user}</div>
                            </div>
                        <div className="col-9">
                        <div className="comment_created_date">{comments.created_date}</div>
                        <div className="text">{comments.text}</div>
                        </div>
                    </div>
                </div>
            )) : null
      );
    }


export function BlogPost(props) {
    const {blogPost} = props;
    const postId = blogPost.id
    console.log(blogPost.comments.length);
    const [isOpen, setIsOpen] = useState(false);
    const toggle = () => setIsOpen(!isOpen);
    const className = props.className ? props.className : 'col-10 mx-auto col-md-6';
    const commentsGood = (<div className="comments-section">
                            <Button color="primary" onClick={toggle} style={{ marginBottom: '0.5rem' }}>Comments</Button>
                            <Collapse isOpen={isOpen}>
                                <div id="comments" className="comments">
                                    <CommentsList comments={blogPost.comments}/>
                                </div>
                            </Collapse>
                        </div>
                        );

    const commentsBad = (<div className="comments-section">
        <div className="comments-bad">
            <p>There are no comments to display.</p>
        </div>
    </div>
    );                   
    let comment = '';
    let user1 = '';
    return (
    <div className="blog-post card">
        <div className='blog-user-info'>
                <div className="blog-user-img col-3">
                    <img src='/logos/silhouette.png'/>
                    <a href={'profile/' + blogPost.user_id}><h6>{blogPost.username}</h6></a>
                    </div>
                    <div className="col-6">
                    </div>
                    <div className="col-3">
                    </div>                                  
            </div>
            <hr/>
    <div className={className}>
        <div className='blog-inner-content'>
                <div className="blog-post-content">
                    <div className="blog-title"><h3><a href={"/blog/"+blogPost.id}>Test Title</a></h3></div>
                    <div className="blog-content"><p>{blogPost.content}</p></div>
        </div>
        </div>
        </div>
        <hr/>
        {blogPost.comments.length > 0 ? commentsGood : commentsBad  }
    </div>
    )
}

export function CommentsList(props) {
    let comments = props.comments
    return (
        <div>
                <Comment comment={comments}/>
        </div>
    )
}




    export function CommentSubmit(props) {
        const [text, setText] = useState("");
        const postId = props.postId;
        const userId = props.userId;
    
        const handleChange = (e) => {
            e.preventDefault();
            if (e.target.name === "text") {
                setText(e.target.value);
            }
        }

        const handleSubmit = ( e ) => {
            e.preventDefault();
            let form_data = new FormData();
            form_data.append('post', postId);
            form_data.append('user', userId);
            form_data.append('text', text);

            try {
                const response = axiosInstanceComment.post('/blogposts/addcomment/', form_data)
            .then((response) => {
                if (response.status === 201) {
                    //window.location.href = '/sent';
                   // document.getElementById("contact-form").style.display = "none";
                    //document.getElementById("message-sent").style.display = "block";
                    setTimeout(function(){
                        window.location.href = '/';
                    }, 3000);
                }
            })
            return response.data; } catch (error) {
                throw error;
            }
        }
        
        return (
            <form id="comment-form-content" onSubmit={handleSubmit} method="POST">
            <div className="comment-form-core">
            <div><h1>Add your comment</h1></div>
            <div className="form-group" id="text">
                <label className="pr-2" for="text">Your comment:</label>
                <textarea name="text" placeholder="Please enter your comment" rows="5" autoComplete="comment text" value={text} onChange={handleChange}></textarea>
            </div>
            </div>
            <div className="submit-button">
            <button type="submit" value="Submit" className="btn btn-primary">Submit</button>
            </div>
        </form>
        )
    }