import React, {useState, useEffect} from 'react'
import { axiosInstance1 } from '../axiosComponents/axiosAPIContUs';

export const ContactUs = () => {
    useEffect (() => {
        document.title ="TLSE: Contact Me";
     }, [])
    const [name, setName] = useState("");
    const [email, setEmail] = useState("");
    const [message, setMessage] = useState("");
    
    const handleChange = (e) => {
        e.preventDefault();
        if (e.target.name === "name") {
          setName(e.target.value);
        } else if (e.target.name === "email") {
          setEmail(e.target.value);
        } else if (e.target.name === "message") {
            setMessage(e.target.value);
          }
      }

    
    const handleSubmit = ( e ) => {
        e.preventDefault();
        let form_data = new FormData();
        form_data.append('name', name);
        form_data.append('email', email);
        form_data.append('message', message);

        try {
            const response = axiosInstance1.post('/contactus/', form_data)
        .then((response) => {
            if (response.status === 201) {
                //window.location.href = '/sent';
                document.getElementById("contact-form").style.display = "none";
                document.getElementById("message-sent").style.display = "block";
                setTimeout(function(){
                    window.location.href = '/';
                 }, 3000);
            }
        })
        return response.data; } catch (error) {
            throw error;
        }
      }
    
    return (
        <div className="contact-page container">
            <div id="contact-form" className="col-12 card">
                <div className="col-6 contactus-pic-col">
                    <img src="./logos/tablewriting.png"/>
                    </div>
        <form id="contact-form-content" className="col-6"onSubmit={handleSubmit} method="POST">
            <div className="contactus-form-core">
            <div><h1>Contact Me</h1></div>
            <div className="form-group">
                <label className="pr-2" for="name">Name:</label>
                <input name="name" placeholder="Enter your name" type="text" autoComplete="contact name" value={name} onChange={handleChange} />
            </div>
            <div className="form-group">
                <label className="pr-2" for="email">Email:</label>
                <input name="email" placeholder="Enter your email" type="email" autoComplete="contact email" aria-describedby="emailHelp" value={email} onChange={handleChange} />
            </div>
            <div className="form-group" id="message">
                <label className="pr-2" for="message">Your message:</label>
                <textarea name="message" placeholder="Please enter your message" rows="5" autoComplete="contact message" value={message} onChange={handleChange}></textarea>
            </div>
            </div>
            <div className="submit-button">
            <button type="submit" value="Submit" className="btn btn-primary">Submit</button>
            </div>
        </form>
        </div>
        <div id="message-sent">
            <h1>Message Sent</h1>
        </div>
        </div>
        )
    }   