export function CurrentProjectsList(props) {
    
    const [currentprojects, setCurrentProjects] = useState([]);
  useEffect(() => {
      const testUpdate = (datatest) => {
        setCurrentProjects(datatest);
      };
      loadCurrentProjects(testUpdate);
    } 
    
  , [])
    return currentprojects.map((item, index)=>{
          return <CurrentProject currentproject={item}/>
            })
}

export function CurrentProject(props) {
    const {currentproject} = props;
    const [planetSpin, setPlanetSpin] = useState(false);
    const spinPlanet = () => {
        setPlanetSpin(true);
      }
      const despinPlanet = () => {
        setPlanetSpin(false);
      }
    return (
                    <div onMouseEnter={spinPlanet} onMouseLeave={despinPlanet} className={planetSpin ? "planet planet"+currentproject.id+" stop" : "planet planet"+currentproject.id}>
                      <div className="project-text">
                        <a className="project-link" href={"/projects/"+currentproject.id}><h3>{currentproject.title}</h3></a>
                      </div>
                  </div>
    )
}