import React, {useEffect} from 'react'

export const BlogDetail = ({ match }) => {
    useEffect (() => {
        document.title = "TLSE Blog: " + match.params.blogId;
     }, [])
    return (
        <div className="blog-detail-page">
            <h3>{match.params.blogId}</h3>
            <div>Test</div>
        </div>
    )
}

