import React from 'react'
import MovingHead from './movinghead'

export const BitAboutMe = () => {
        return (
            <div className="aboutme-section">
                <MovingHead> Hello!!!!!! </MovingHead>
                <div className="container col-holder-aboutme">
                    <div className="col-6 biography">
                        <div className="biography-content">
                            <h2>Tom Lewis<br/>Software Engineering</h2>
                            <p>I'm a developer specialising in Python, Django, Flask, Falcon, JavaScript, React, API, PHP, Moodle and more.</p>
                        </div>
                    </div>
                    <div className="col-6 biography-picture">
                        <div className="biography-picture">
                            <img className="frame" src="./logos/frame.png" alt=""/>
                            
                        </div>
                    </div>
                </div>
            </div>
        )
    }