import axios from 'axios';
import React, {useState, useEffect} from 'react'
import { Button, Modal, ModalBody, ModalFooter, ModalHeader } from 'reactstrap';
import { axiosInstance } from '../axiosComponents/axiosApi';
import { BlogPost, BlogPostsList } from '../blog';
import { createBlogPost } from '../lookup';
import { IdCard, MapPin, Code, Feather } from './extracomponents/svgs';
import {loadBlogPosts} from '../lookup'
import { axiosInstanceBlog } from '../axiosComponents/axiosApiBlog';

export const ProfileComponent = ({ match }) => {
    const [profileData, setProfileData] = useState('');
    const [modal, setModal] = useState(false);
    const toggle = () => setModal(!modal);
    useEffect (() => {
        document.title = "TLSE: User Profile";
        document.getElementById('user-profile-page').style = 'background: #eee;';
        const accessToken = localStorage.getItem('access_token');
        try { const response = axiosInstance.get('/profile/'+ match.params.usernameID)  
        .then((response) => {
        axiosInstance.defaults.headers['Authorisation'] = "JWT " + accessToken;
          setProfileData(response.data);
          console.log(profileData);
        }) 
        return profileData;
    } catch (error) {
        throw error
     }
     }, [])
    return (
        <div id="user-profile-page" className="user-profile-page">
            <div className="profile-header-image">
                <div className="col-4 img-holder"><img src="/logos/silhouette.png"/></div>
                <div className="col-8 username-holder"><h1>{profileData.username}</h1></div>              
            </div>
            <div className="main-profile-section container">           
            <div className="profile-container col-3">
                <h2 >Vital Statistics</h2>
                <div className="card user-profile-content">
                <div className="username-profile"><div className="col-3 mr-2"><IdCard/></div><div className="col-9"><h3>{profileData.name}</h3></div></div>
                <div className="location"><div className="col-3 mr-2"><MapPin/></div><div className="col-9"><h3>{profileData.location}</h3></div></div>
                <div className="fav-lang"><div className="col-3 mr-2"><Code/></div><div className="col-9"><h3>{profileData.favourite_prog_lang}</h3></div></div>
                </div>
                <h2 className="bio-heading">Bio</h2>
                <div className="card user-profile-bio">
                    <div className="profile-bio">
                        <p>{profileData.aboutme}</p>
                    </div>
                </div>
            </div>
            <div className="profile-user-blog-posts col-9">
                <h2>Blog Posts:</h2> 
                <BlogPostsListProfile userID={match.params.usernameID}/>
            </div>
            </div> 
            <div className="post-modal">
                <Button color="danger" onClick={toggle}><Feather/></Button>
                <Modal isOpen={modal} toggle={toggle}>
                <ModalHeader toggle={toggle}>Write your blog post</ModalHeader>
                <ModalBody>
                <div className="profile-newpost"><BlogsProfileComp/></div>      
                </ModalBody>
                <ModalFooter>
                    <Button color="primary" onClick={toggle}>Do Something</Button>{' '}
                    <Button color="secondary" onClick={toggle}>Cancel</Button>
                </ModalFooter>
                </Modal>
            </div>
        </div>
    )
}


export function BlogsProfileComp(props) {
    const textAreaRef = React.createRef()
    const [newBlogPosts, setNewBlogPosts] = useState([])
    const [userId, setUserId] = useState ('');
    useEffect(() => {
        const accessToken = localStorage.getItem('access_token');
        try {
          const response = axiosInstance.get('/currentuser/')
        .then((response) => {
            axiosInstance.defaults.headers['Authorization'] = "JWT " + accessToken;
            setUserId(response.data.id)
        });
        return userId 
        } catch (error) {
        throw error;
        }
      }, [userId]);

    const handleSubmit = (event) => {
        event.preventDefault()
        const newVal = textAreaRef.current.value
        let tempNewBlogPosts = [...newBlogPosts]
        let form_data = new FormData();
        form_data.append('user', userId);
        form_data.append('content', newVal);
        createBlogPost(form_data);
        setNewBlogPosts(tempNewBlogPosts)
        textAreaRef.current.value = ''
    }
    return (
            <form onSubmit={handleSubmit}>
                <textarea ref={textAreaRef} required={true} className='form-control' name='tweet'>
                </textarea>
                <button type='submit' className='btn btn-primary my-3'>Post</button>
            </form>
    )
}


export function BlogPostsListProfile(props) {
    const [userBlogList, setUserBlogList] = useState([]);
    const userID = props.userID;
  useEffect(() => {
      try {
        const response = axiosInstanceBlog.get('/blogposts/user/' + userID)
        .then((response) => {
              //console.log(response.data);
              setUserBlogList(response.data);
              return userBlogList;
          }) 
          return userBlogList;
      } catch (error) {
          throw error
       }
       }, [])
    return userBlogList.map((item, index)=>{
          return <BlogPost blogPost={item} className={'blog-post-card'} key={`${index-item.id}`}/>
            })
}