import React, {useState} from 'react';
import { axiosInstance } from '../axiosComponents/axiosApi';
import { FacebookSVG, GoogleSVG, LinkedInSVG } from './svgs';


export const Login = (props) => {
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");

  const handleChange = (e) => {
    if (e.target.name === "username") {
      setUsername(e.target.value);
    } else if (e.target.name === "password") {
      setPassword(e.target.value);
    }
  }


  const handleSubmit = (e) => {
        e.preventDefault();
        try {
            const response = axiosInstance.post('/token/obtain/', {
                username: username,
                password: password
        })
        .then((response) => {
            axiosInstance.defaults.headers['Authorisation'] = "JWT " + response.data.access;
            localStorage.setItem('access_token', response.data.access);
            localStorage.setItem('refresh_token', response.data.refresh);
            if (response.status === 200) {
                window.location.href = '/';
            }
        })
        return response.data;  
  } catch (error) {
      throw error;
  }
}

        return (
                <div className="login-page">
                <div class="form-container sign-in-container">
                    <form onSubmit={handleSubmit}>
                        <h1>Sign in</h1>
                        <div class="social-container">
                            <a href="#" className="google-signup-link"><GoogleSVG/></a>
                            <a href="#" className="facebook-signup-link"><FacebookSVG/></a>
                            <a href="#" className="linkedin-signup-link"><LinkedInSVG/></a>
                        </div>
                        <span>or use your account</span>
                        <div className="col-12 mt-2 mb-">
                            <input name="username" placeholder="Username" type="text" autoComplete="login username" value={username} onChange={handleChange}/>
                        </div>
                        <div className="col-12 mt-2 mb-2">
                        <input name="password" placeholder="Password" type="password" autoComplete="login password"value={password} onChange={handleChange}/>
                        </div>
                        <div className="forgot-password col-12 mt-3 mb-3">
                            <a href="#">Forgot your password?</a>
                        </div>
                        <div className="col-12">
                        <input className="btn login-submit-button" type="submit" value="Submit"/>
                        </div>                        
                    </form>
                </div>
            </div>
)
}
              {/*<form onSubmit={handleSubmit}>
                  <label>
                      Username:
                      <input name="username" type="text" autoComplete="login username" value={username} onChange={handleChange}/>
                  </label>
                  <label>
                      Password:
                      <input name="password" type="password" autoComplete="login password"value={password} onChange={handleChange}/>
                  </label>
                  <input type="submit" value="Submit"/>
              </form>
        </div>*/}
 