import React, { useState, useEffect } from "react";
import { Collapse, Nav, NavbarToggler, NavItem } from "reactstrap";
import { axiosInstance } from "../../axiosComponents/axiosApi";
import { NavLink as RRDNavLink } from 'react-router-dom';

const handleLogout = (e) => {
    e.preventDefault();
    try {
        const response = axiosInstance.post('/blacklist/', {
            "refresh_token": localStorage.getItem("refresh_token")
        })
        .then((response) => {
          localStorage.removeItem('access_token');
          localStorage.removeItem('refresh_token');
          axiosInstance.defaults.headers['Authorization'] = null;
          if (response.status === 205) {
            window.location.href = '/';
          }
        }); 
        
        return response.data;
    }
    catch (error) {
        console.log(error);
    }
  }

export const GetCurrentUser = (props) => {
const [username, setUsername] = useState ('');
const [userId, setUserId] = useState ('');
const toggle = props.toggle
const isOpen = props.isOpen
const not_logged_in = (
    <div className="nav-usermenu">
<NavbarToggler onClick={toggle} />
  <Collapse id="navbartest" isOpen={isOpen} navbar>
    <Nav className="d-flex float-right" navbar>
          <NavItem>
              <RRDNavLink className={"nav-link"} to={"/login/"}>Login</RRDNavLink>
          </NavItem>
          <NavItem>
              <RRDNavLink className={"nav-link"} to={"/signup/"}>Signup</RRDNavLink>
          </NavItem>
    </Nav>
  </Collapse>
  </div>
)

const logged_in = (
    <div className="nav-usermenu">
<NavbarToggler onClick={toggle} />
  <Collapse id="navbartest" isOpen={isOpen} navbar>
    <Nav className="d-flex float-right" navbar>
          <NavItem className="mr-2">
              <h5 className="username"><a href={"/profile/"+ userId}>{username}</a></h5>
          </NavItem>
          <NavItem>
          <button className="logout-btn" onClick={handleLogout}>Logout</button>
          </NavItem>
    </Nav>
  </Collapse>
  </div>
)

  useEffect(() => {
    const accessToken = localStorage.getItem('access_token');
    try {
      const response = axiosInstance.get('/currentuser/')
    .then((response) => {
        axiosInstance .defaults.headers['Authorization'] = "JWT " + accessToken;
        setUserId(response.data.id);
        setUsername(response.data.username);
    });
    return response.data;  
    } catch (error) {
    throw error;
    }
  }, [setUsername])
  return (
      username ?
      logged_in : 
      not_logged_in
      
  )
}