import React, { useState, useEffect } from 'react';
import {
  Collapse,
  Navbar,
  NavbarToggler,
  NavbarBrand,
  Nav,
  NavItem,
  NavLink
} from 'reactstrap';
/*import Slide from 'react-reveal/Slide'*/
import { NavLink as RRDNavLink, useLocation } from 'react-router-dom';
import './navbar.scss';
import { ConditionalNav, GetCurrentUser } from '../home/extra-components/currentuser';

export const NavbarFinal = (props) => {
  const [isOpen, setIsOpen] = useState(false);
  const toggle = () => setIsOpen(!isOpen);
  const [navbar, setNavbar] = useState(false);
  const [navbarHome, setNavbarHome] = useState(false);
  const location = useLocation();
  
  useEffect(() => {
    if (location.pathname === "/") {
      console.log("meh");
      setNavbarHome(true);
      console.log(navbarHome);
    } else {setNavbarHome(false)}
  }, [navbarHome, location.pathname])

const changeBackground = () => {
  if(window.scrollY >= 70) {
    setNavbar(true);
  } else {
    setNavbar(false);
  }
}

window.addEventListener('scroll', changeBackground)
//window.addEventListener('load', HeaderView)

  return (
      <Navbar className={navbar ? 'navbar active' : 'navbar'} id={navbarHome ? "navbar-tlse-home" : "navbar-tlse"} light expand="md">
        <NavbarBrand href="/" id="navbar-brand" className={navbar ? 'navbarBrand active rotate mr-4' : 'navbarBrand mr-4'}><img src="/logos/logotlse.png" alt=""/></NavbarBrand>
        <NavItem className={navbar ? 'nav-link active mr-2' : 'nav-link mr-2'}>
              <RRDNavLink to="/blog" activeClassName="selected">Blog</RRDNavLink>
          </NavItem>
          <NavItem className={navbar ? 'nav-link active' : 'nav-link'}>
              <RRDNavLink to="/about" activeClassName="selected">About</RRDNavLink>
          </NavItem>
          <NavItem className={navbar ? 'nav-link active' : 'nav-link'}>
              <RRDNavLink className="aboutme btn" to="/contactme" activeClassName="selected">Contact Me</RRDNavLink>
              {/*<NavLink className="aboutme btn" datatoggle="modal" datatarget="#contactMe" href='#'>Contact Me</NavLink>*/}
          </NavItem>
            <GetCurrentUser toggle={toggle} isOpen={isOpen} />
      </Navbar>
  );
}

